<template>
  <div>
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline>
        <el-form-item prop="logType">
          <el-select v-model="query.logType" filterable clearable placeholder="日志类型" style="width: 178px;" class="filter-item">
            <el-option
              v-for="(item,index) in logType"
              :key="index"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <el-form-item prop="username">
          <el-input v-model="query.username" clearable placeholder="用户名" style="width: 178px;" class="filter-item" />
        </el-form-item>
        <el-form-item prop="description">
          <el-input v-model="query.description" clearable placeholder="请求描述" style="width: 178px;" class="filter-item" />
        </el-form-item>
        <el-form-item prop="address">
          <el-input v-model="query.address" clearable placeholder="请求地址" style="width: 178px;" class="filter-item" />
        </el-form-item>
        <el-form-item prop="startTime">
          <el-date-picker
            v-model="query.startTime"
            class="filter-item"
            type="date"
            style="width: 178px;"
            value-format="yyyy-MM-dd"
            placeholder="开始时间"
            @change="query.endTime = ''"
          />
        </el-form-item>
        <el-form-item prop="endTime">
          <el-date-picker
            v-model="query.endTime"
            class="filter-item"
            type="date"
            style="width: 178px;"
            value-format="yyyy-MM-dd"
            placeholder="结束时间"
            :picker-options="{disabledDate: (time) => time.getTime() < new Date(query.startTime).getTime()}"
          />
        </el-form-item>

        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="init"
        >搜索</el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        >重置</el-button>
      </el-form>
    </div>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :selection="false"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="210"
        :show-operate="true"
        :table-column-sort="false"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <el-button :disabled="!scope.row.params" type="primary" icon="el-icon-eye" @click="handleViewParams(scope.row)">请求参数</el-button>
          <el-button :disabled="!scope.row.exceptionDetail" type="primary" icon="el-icon-eye" @click="handleViewException(scope.row)">异常信息</el-button>
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
    <params-dialog v-model="dialogVisible" title="请求参数" :dialog-form="dialogForm" :extra-info="{}" />
    <exception-dialog v-model="dialogVisible1" title="异常信息" :dialog-form="dialogForm" :extra-info="{}" />
  </div>
</template>
<script>
import { getListApi } from '@/apis/modules/base/baseOperationLog'
import paginationPage from '@/mixins/paginationPage'
import ParamsDialog from './ParamsDialog'
import ExceptionDialog from './ExceptionDialog'
export default {
  components: {
    ParamsDialog,
    ExceptionDialog
  },
  mixins: [paginationPage],
  data() {
    return {
      title: this.$route.meta.title,
      query: {
        logType: '', // 日志类型
        username: '', // 用户名
        description: '', // 请求描述
        address: '', // 请求地址
        startTime: '', // 开始时间
        endTime: '' // 结束时间
      },
      loading: false,
      tableData: [],
      logType: [
        { value: 'INFO', label: 'INFO' },
        { value: 'ERROR', label: 'ERROR' }
      ],
      columnList: [
        {
          module: 'word',
          prop: 'logType',
          label: '类型',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'address',
          label: '请求地址',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'method',
          label: '方法名',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'username',
          label: '操作人',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'description',
          label: '请求描述',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'requestIp',
          label: '请求IP',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        // {
        //   module: 'word',
        //   prop: 'params',
        //   label: '请求参数',
        //   align: 'center',
        //   fixed: false,
        //   sortable: false,
        //   resizable: true,
        //   formatter: null,
        //   headerAlign: 'center'
        // },
        // {
        //   module: 'word',
        //   prop: 'exceptionDetail',
        //   label: '异常信息',
        //   align: 'center',
        //   fixed: false,
        //   sortable: false,
        //   resizable: true,
        //   formatter: null,
        //   headerAlign: 'center'
        // },
        {
          module: 'word',
          prop: 'createTime',
          label: '请求时间',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'browser',
          label: '浏览器',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      dialogVisible: false,
      dialogVisible1: false,
      dialogForm: {}
    }
  },
  computed: {
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getListApi({
          page: {
            ...this.pageInfo
          },
          ...this.query
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleViewParams(row) {
      this.dialogVisible = true
      this.dialogForm = row
    },
    handleViewException(row) {
      this.dialogVisible1 = true
      this.dialogForm = row
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    },
    handleRowClick({ row, column, cell, event }) {
      console.log(row)
      if (column.label === '操作') {
        return false
      }
    }
  }
}
</script>
