<template>
  <el-dialog
    :title="title"
    top="5vh"
    :visible="value"
    :show-close="true"
    width="60vw"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-form ref="form" :model="form">
      <el-form-item prop="exceptionDetail" :style="{height: dialogHeight}">
        <el-input
          v-model.trim="form.exceptionDetail"
          class="textarea"
          disabled
          type="textarea"
          style="width: 100%; height: 100%"
          resize="none"
        />
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="beforeClose">关 闭</el-button>
      <!-- <el-button @click="beforeClose">取 消</el-button>
      <el-button type="primary" @click="handleConfirm">确 定</el-button> -->
    </span>
  </el-dialog>
</template>

<script>
export default {
  props: {
    value: {
      required: true,
      type: Boolean
    },
    title: {
      type: String,
      default: ''
    },
    dialogForm: {
      type: Object,
      default: () => ({})
    },
    extraInfo: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      dialogHeight: document.documentElement.clientHeight * 0.43 + 92 + 38 + 'px',
      form: {
        exceptionDetail: ''
      }
    }
  },
  methods: {
    initSelect() {
      this.form = { exceptionDetail: this.dialogForm.exceptionDetail }
    },
    isStringSerializable(str) {
      try {
        JSON.parse(str)
        return true
      } catch (e) {
        return false
      }
    },
    beforeClose() {
      this.$emit('input', false)
    },
    handleConfirm() {
      this.beforeClose()
    }
  }
}
</script>

<style scoped lang="scss">
::v-deep .el-form .el-form-item .el-form-item__content {
    height: 100%;
}
::v-deep .textarea .el-textarea__inner {
    height: 100%;
}
</style>

